import React, {useEffect, useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import axios from 'axios';
import Autocomplete from '@material-ui/lab/Autocomplete'
import TextFieldLite from '../../../common/TextFieldLite';

const SearchField = ({get,label,noOptionsText}) => {
  const classes = useStyles();

  const [ingredients, setIngredients] = useState([]);
  const getCategories = () => {
    try {
      //setLoading(true);
      axios.get(get,
      ).then(data => {
        setIngredients(data.data);
      });
    } catch (e) {
      console.error(e);
    }
  };
  useEffect(() => {
    getCategories();
  }, []);
  return (
      <>
        <Autocomplete
            options={ingredients.map((option) => option.title)}//option.title.map((option2)=> option2.title.elem
            blurOnSelect = 'touch'
            clearOnBlur
            renderTags
            noOptionsText ={noOptionsText}
            onOpen = {getCategories}
            style={{ width: 300 }}
            renderInput={(params) => (
                <TextFieldLite {...params} label = {label}   margin="normal" variant="outlined" size='small' /*value = {elem}*/
                />
            )}
        />
      </>
  );
};

const useStyles = makeStyles((theme) => ({
  SummaryRightPanelSearchLine: {
    position: 'relative',
    display: 'flex',
    justifyContent: 'center',
  },
  searchField: {
    backgroundColor: 'rgba(218, 218, 218, 0.5)',
    border: 'none',
    height: '29px',
    margin: '10px 0',
    paddingLeft: '40px',
    flexBasis: '100%',
    maxWidth: '500px',
    '&:focus': {
      outline: 'none',
    },
    [theme.breakpoints.down('xs')]: {
      maxWidth: 'initial',
    },
  },

  searchIcon: {
    fill: '#fff',
    position: 'absolute',
    top: '13px',
    left: '10px',
  },
}));

export default SearchField;
